<template>
  <div class="header">
    <ul class="header-button-left">
      <li @click="step=3">Search</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step==1" @click="step++">Next</li>
      <li v-if="step==2" @click="publish">Post</li>
      <li v-if="step==3" @click="step=0">Cancel</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>
  <!-- <p>{{ $store.state.more }}</p>
  <button @click="$store.dispatch('getData')">ÎçîÎ≥¥Í∏∞</button> -->
  <!-- <p>{{name}} {{age}} {{likes}}</p>
  <button @click="$store.commit('add', 10)">Î≤ÑÌäº</button>
  <button @click="add(10)">Î≤ÑÌäº</button> -->
  <Container @write="postContent = $event" :instaData="instaData" :step="step" :image="image" />
  <button v-if="step==0" @click="more">ÎçîÎ≥¥Í∏∞</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" multiple accept="image/*" type="file" id="file" class="inputfile" />
      <label v-if="step==0" for="file" class="input-plus">+</label>
    </ul>
  </div>
</template>

<script>
import Container from './components/Container.vue';
import data from './assets/data';
import axios from 'axios';
// import {mapState, mapMutations} from 'vuex';

axios.post()

export default {
  name: 'App',
  data(){
    return {
      step : 0,
      instaData : data,
      morePost : 0,
      image : '',
      postContent : '',
      selected : '',
    }
  },
  mounted(){
    this.emitter.on('clickFilter', (a)=>{
      this.selected = a
    })
  },
  components: {
    Container,
  },
  // computed: {
  //   ...mapState(['name', 'age', 'likes']),
  //   ...mapState({ myName: 'name', }),
  // },
  methods: {
    // ...mapMutations(['setMore', 'instaLike', 'add']),
    more(){
      axios.get(`https://codingapple1.github.io/vue/more${this.morePost}.json`)
      .then(result=>{
        this.instaData.push(result.data);
        this.morePost++;
      })
    },
    upload(e){
      var file = e.target.files;
      console.log(file[0]);
      let url = URL.createObjectURL(file[0]);
      console.log(url);
      this.image = url;
      this.step++;
    },
    publish(){
      var newPost = {
        name: "Ian Lee",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.image,
        likes: this.likes,
        date: "May 15",
        liked: false,
        content: this.postContent,
        filter: this.selected
      };
      this.instaData.unshift(newPost);
      this.step = 0;
    },
  },
}
</script>

